import { React, useEffect, useState } from "react";
import styled from "styled-components";
import { useNavigate, useLocation } from "react-router-dom";
import LeftArrow from "../assets/images/icons/left_arrow.png";
// import Close from "../assets/image/Clear.png";
import Tag from "../assets/image/headerLogo.png"
const HeaderWrapper = styled.div`
  display: flex;
  justify-content: ${(props) => props.showquestionbar !== "false" || props.back
      ? "flex-start"
      : "space-between"};
  box-sizing: border-box;
  border-bottom: 1px solid #d9d9d9;
  /* height: 66.92px; */
  height: 3.5em;
  align-items: center;
  /* letter-spacing: -7%; */
`;

const LeftArrowBtn = styled.button`
  cursor: pointer;
  img {
    margin-right: 0.28875em;
    width: 1.730625em;
  }
`;

const CloseBtn = styled.button`
  cursor: pointer;
  img {
    /* margin-right: 5px; */
    width: 1.730625em;
  }
`;

const Typography = styled.p`
  margin: 0 0.72125em 0 0;
  strong {
    font-weight: bold;
  }
`;

const Header = ({current, count, back }) => {
  const navigate = useNavigate();
  const loation = useLocation();
  const [showQuestionBar, setShowQuestionBar] = useState(false);

  const handleBack = () => {
    if (current === "1") {
      navigate("/");
    } else {
      navigate(-1);
    }
  };

  // const handleGohome = () =>{
  //   navigate("/");
  // }

  useEffect(() => {
    const path = loation.pathname.split("/");
    if (path[1] === "question") {
      setShowQuestionBar(true);
    }
  }, []);

  return (
    <HeaderWrapper showquestionbar={showQuestionBar.toString()} back={back}>
      {(showQuestionBar || back) && (
        <LeftArrowBtn>
          <img src={LeftArrow} alt="back" onClick={handleBack} />
        </LeftArrowBtn>
      )}
      <Typography>
        <img src={Tag} width="100px" height="30px" style={{ marginTop: "10px" }} />
      </Typography>
      {showQuestionBar && (
        <Typography>
          <strong>{current}</strong> / {count}
        </Typography>
      )}
      {/* {!showQuestionBar && !back && (
        <CloseBtn onClick={handleGohome}>
          <img src={Close} alt="close" />
        </CloseBtn>
      )} */}
    </HeaderWrapper>
      
  );
      }

  
export default Header;
